/* About Section */
.about-section {
  padding: 4rem 0;
  background-color: #ffffff;
  padding: 20px;
  margin: 20px;
}

.container {
  max-width: 1200px;
  margin: 0 auto; /* Center the container */
  padding: 10px;
}

.about-content {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 2rem;
}

/* About text */
.about-text {
  flex: 1;
  font-size: 1.2rem;
  line-height: 1.8;
}

/* About image */
.about-image {
  flex: 1;
  text-align: center;
}

.about-image img {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
}

/* Responsive Adjustments */

/* For small devices (phones, up to 480px) */
@media (max-width: 480px) {
  .about-content {
    flex-direction: column; /* Stack text and image vertically */
    text-align: center;
  }

  .about-text {
    font-size: 1rem;
    line-height: 1.6;
    padding: 0 10px; /* Add padding for better readability on small screens */
  }

  .about-section {
    margin: 10px; /* Smaller margin for compact layout */
  }
}

/* For medium devices (tablets, up to 768px) */
@media (max-width: 768px) {
  .about-content {
    flex-direction: column;
    text-align: center;
  }

  .about-text {
    font-size: 1.1rem;
  }

  .about-section {
    padding: 3rem 1rem; /* Reduce padding slightly */
  }
}

/* For large devices (desktops, up to 1200px) */
@media (max-width: 1200px) {
  .container {
    padding: 0 1rem; /* Add some padding */
  }
  
  .about-text {
    font-size: 1.15rem;
  }
}
